export default class Intro {
  constructor() {
    this.init();
    const COURSE_All_INTRO = 'COURSE_TASK_INTRO';
    const COURSE_BASE_INTRO = 'COURSE_BASE_INTRO'; 
    const COURSE_TASK_INTRO = 'COURSE_TASK_INTRO';
  }

  init() {

    console.log(this.isHaveTaskCreateStep());

    //


    //第一次重基本信息进来,做三步引导
    if(this.isBaseInfoFristEnter()) {
      //...
      return;
    }

    //第一次次从计划任务进来

    //判断基本信息做过引导，显示一步引导
    if(this.isBaseInfoFristEnter()) {

      return;
    }

    //判断基本信息未做过引导，显示4步引导
  }

  isTaskCreateStep() {
    return $('#step-4').length;
  }

  isCourseBaseIntro() {
    if (store.get(COURSE_All_INTRO)) {
      //先进入的任务页,做了四部引导
      return false;
    }
    if (!store.get(COURSE_BASE_INTRO)) {
      store.set(COURSE_BASE_INTRO, true);
      return true;
    }
  }

  isBaseInfoFristEnter() {
     
    return true;
  }
}







